<template>
  <b-container id="home" class="bv-example-row">
    <b-row align-v="center">
      <b-card
        v-for="(item, index) in homeData"
        :key="index"
        :title="item.title"
        :imgUrl="item.season_img"
        :pageId="item.id"
      ></b-card>
    </b-row>
    <div class="overflow-auto">
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="center"
          pills
          @input="paginate(currentPage)"
        ></b-pagination>
      </div>
    </div>
  </b-container>
</template>

<script>
//引入组件
import Card from "@/components/Card.vue";
export default {
  name: "home",
  components: { "b-card": Card },
  data() {
    return {
      homeData: {},
      perPage: 5,
      currentPage: 1,
      totalRows: 0,
    };
  },
  methods: {
    paginate(currentPage) {
      this.requestData(currentPage);
    },
    requestData(currentPage) {
      this.$axios
        .get(`http://localhost:3000/api/getData?p=${currentPage}`)
        .then((res) => {
          this.perPage = res.data.results.perPage;
          this.totalRows = res.data.results.totalRows;
          this.homeData = res.data.results.resData;
        })
        .catch((err) => {
          console.log("数据获取异常：", err);
        });
    },
  },
  created() {
    //let id = this.$route.query.id;
    this.requestData(1);
  },
};
</script>

<style scoped>
</style>



