<template>
  <div>
      <b-media tag="li" class="py-2">
        <template #aside>
          <b-img
            class="rounded"
            blank
            :blank-color="'#'+Math.floor(Math.random()*0xffffff).toString(16)"
            width="140"
            height="120"
            alt="placeholder"
          ></b-img>
        </template>
        <h5 class="mt-0 mb-1">
            <b-link>{{ title }}</b-link>
        </h5>
        <p class="mb-0" size="sm">
          {{plot}}
        </p>
        <b-button-group size="sm">
          <b-button pill variant="outline-danger">
            <b-icon-play-fill />
          </b-button><hr/>
          <b-button variant="success">Success</b-button>
          <b-button variant="info">
            <b-icon-play />
          </b-button>
          <b-button variant="warning">Warning</b-button>
        </b-button-group>
      </b-media>
  </div>
</template>

<script>
export default {
    props: ["title","plot"],
};
</script>

<style scoped>
</style>


