<template>
  <div>
    <b-col>
      <article class="post post-medium" style>
        <div class="thumbr">
          <a class="post-thumb" href>
            <span class="play-btn-border" title="Play">
              <b-icon class="headline-round" icon="people-fill" style="width: 60px; height: 60px"></b-icon>
            </span>
            <b-img-lazy
              class="img-responsive"
              blank
              :blank-color="'#'+Math.floor(Math.random()*0xffffff).toString(16)"
              :src="'https://picsum.photos/240/320/?image='+Math.floor((Math.random()*100)+1)"
              alt="#"
            ></b-img-lazy>
          </a>
        </div>
        <div class="infor" style="text-align:center">
          <h5>
            <a class="title" href="#">{{name}}</a>
          </h5>
          <span class="posts-txt" title="Posts from Channel">
            <b-icon icon="arrow-up" variant="danger"></b-icon>扮演{{role}}
          </span>
        </div>
      </article>
    </b-col>
  </div>
</template>

<script>
export default {
  props: ["name","role"],
};
</script>

<style scoped>
</style>


