<template>
  <div>
    <b-col>
      <b-card
        :title="title"
        :img-src="imgUrl"
        img-alt="Job-info"
        img-top
        tag="article"
        style="max-width: 20rem"
        class="mb-2"
      >
        <b-card-text>
        </b-card-text>
        <b-link :href="'/#/details?p='+pageId" class="card-link" style="color:#000">Another link去往页面 {{
          pageId
        }}</b-link>
      </b-card>
    </b-col>
  </div>
</template>

<script>
//img-src="https://picsum.photos/600/300/?image=25"
export default {
  props: ["title", "imgUrl", "pageId"],
};
</script>

<style scope>
</style>


